<template>
  <div class="price-section section py-5 ">
    <div class="bg"
      style="background-image:url('/static/img/index_cost.jpg')"></div>
    <div class="section-header">
      <h2 class="text-white">立即享受音樂的陪伴</h2>
    </div>
    <div class="container container-sm">
      <div class="card-deck">
        <div class="card"
          v-for="(item,k) in packs"
          :key="k">
          <div class="card-header ">
            <h3>{{item.title}}</h3>
            <p class="card-text text-muted"
              v-html="item.desc"></p>
          </div>
          <div class="card-body"> {{item.priceInfo}} </div>
          <div class="card-footer">
            <button @click="buy(item.id)"
              class="btn btn-primary">立即體驗</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {

  name: 'price',

  computed: {
    ...mapGetters({
      // 取得資料夾列表
      packs: 'global/packs'

    }),
  },
  methods: {
    buy () {
      this.$router.push({ name: 'register' })
    }
  }

};
</script>
<style lang="scss" scoped>
@import "../../assets/scss/custom-bootstrap.scss";
.card {
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
}
.card-header {
  background: transparent;
  border-bottom-width: 0;
  text-align: center;
  padding: 3rem 1rem 0;
  h3 {
    border-bottom: 1px solid $primary;
    padding: 1rem 0 1.5rem;
    margin-bottom: 1.5rem;
    font-size: 1.1rem;
    font-weight: bold;
  }
}

.card-body {
  text-align: center;
  padding: 4rem 0;
  font-size: 1.6rem;
}
.card-footer {
  background: transparent;
  border-top-width: 0;
  text-align: center;
  padding: 1rem 0 3rem;
}
</style>



